/* CSS RESET */
*, *::before, *::after{
  box-sizing: border-box;
}
img, picture, svg, video{
  display: block;
  max-width: 100%;
}
input, textarea, button, select{
  font: inherit;
}
*{
  margin:0;
  padding: 0;
  font: inherit;
  line-height: 160%;
  /* outline: 1px solid lime; */
}
body{
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
}
:root{
  --light-blue: #BCE0FF;
  --pes-orange: #FF7426;
  --pes-navy: #2F327D;
  --pes-gray: #696984;
  --section-margin: 100px;
  --fs-11: 0.688rem;
  --fs-16: 1rem;
  --fs-24: 1.5rem;
  --fs-36: 2.25rem;
  --fs-54: 3.375rem;
  --fs-81: 5.063rem;
}

.bold{
  font-weight: 700;
}
.text-navy{
  color: var(--pes-navy);
}
.text-orange{
  color: var(--pes-orange);
}
.text-gray{
  color: var(--pes-gray);
}
.text-white{
  color: white;
}
.underline{
  text-decoration: underline;
}
.rounded{
  border-radius: 500px;
}
.bold{
  font-weight: 700;
}
.italic{
  font-style: italic;
}
.col{
  display: flex;
  flex-direction: column;
}
.row{
  display: flex;
  flex-direction: row;
}
.grid{
  display: grid;
}
section + section{
  margin-top: var(--section-margin);
}




.section{
  padding-inline: 20px;
}

/* -------------HERO SECTION------------- */
.hero-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  aspect-ratio: 390/587;
  background-image: url(../Assets/Images/hero-bg-mobile.png);
  background-size: cover;
  background-repeat: no-repeat;
}
.nav-mobile{
  background-color: #BCE0FF;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  padding-inline: 20px;
}
.pes-logo{
  background-image: url(../Assets/Images/pes-logo.png);
  height: 50px;
  width: 106px;
  background-size: cover;
}
.nav-menu-icon{
  background-image: url(../Assets/Images/menu.svg);
  width: 36px;
  height: 36px;
}
.hero-text-section{
  padding-top: 40px;
  align-items: center;
}
.hero-section-buttons{
  font-size: var(--fs-11);
  display: flex;
  flex-direction: row;
  width: fit-content;
  margin: 20px auto;
  align-items: center;
}
.book-free-trial-button{
  border: none;
  display: grid;
  place-items: center;
  background-color: var(--pes-orange);
  width: 106px;
  aspect-ratio: 106/34;
  margin-right: 20px;
}
.watch-button{
  border-radius: 100%;
  height: 34px;
  width: 34px;
  border: none;
  margin-right: 5px;
  background-color: white;
  display: grid;
  place-items: center;
}
.hero-section h1{
  max-width: 18ch;
  font-size: var(--fs-24);
  margin: auto;
  margin-bottom: 10px;
}



/* -------BADGES SECTION-------- */
.badges-section{
  align-items: center;
  text-align: center;
}
.badges-section h1{
  font-size: var(--fs-16);
  max-width: 24ch;
}
.badge{
  aspect-ratio: 1;
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
}
.badges{
  margin-top: 10px;
  justify-content: space-between;
  width: 80%;
}
.badge-1{
  background-image: url(../Assets/Images/client-1.png);
}
.badge-2{
  background-image: url(../Assets/Images/client-2.png);
}
.badge-3{
  background-image: url(../Assets/Images/client-3.png);
}
.badge-4{
  background-image: url(../Assets/Images/client-4.png);
}
.badge-5{
  background-image: url(../Assets/Images/client-5.png);
}




/* WHO WE ARE SECTION */
.who-we-are-section{
  margin: auto;
  text-align: center;
}
.who-we-are-section h1{
  font-size: var(--fs-24);
  margin-bottom: 10px;
}
.frames-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.who-we-are-frame-container{
  width: 100%;
}
.who-we-are-frame-container + .who-we-are-frame-container{
  margin-top: 20px;
}
.frame{
  background-size: cover;
  aspect-ratio: 286/164;
  display: grid;
  place-items: center;
  border-radius: 40px;
}
.learn-at-centre{
  background-image: url(../Assets/Images/Rectangle19.png);
}
.learn-at-home{
  background-image: url(../Assets/Images/Rectangle21.png);
}
.join-button{
  font-size: var(--fs-11);
  border: none;
  display: grid;
  place-items: center;
  margin-inline: auto;
  background-color: var(--pes-orange);
  width: 120px;
}


/* --------UNLEASH SECTION---------- */
.unleash-section{
  text-align: center;
  align-items: center;
}
.unleash-section h1{
  font-size: var(--fs-24);
  max-width: 25ch;
  margin: auto;
  margin-bottom: 10px;
}
.unleash-section p{
  margin-bottom: 20px;
}
.unleash-video{
  background-image: url(../Assets/Images/Group5453.png);
  aspect-ratio: 800/536;
  background-size: contain;
  background-repeat: no-repeat;
}
.unleash-video-caption{
  margin-top: 10px;
  font-size: var(--fs-11);
}

/* ------------OUR SUCCESS-------------- */
.success-section{
  text-align: center;
}
.success-numbers{
  margin: auto;
  margin-bottom: 10px;
  max-width: 340px;
  justify-content: space-between;
}
.success-section h1{
  font-size: var(--fs-24);
  margin-bottom: 10px;
}
.success-numbers h1{
  background: -webkit-linear-gradient(#136CB5, #49BBBD);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.success-numbers h2{
  font-size: var(--fs-11);
}
.success-section p{
  margin: 8px auto;
}

/* -------TRACKS SECTION----------- */
.tracks-section{
  text-align: center;
}
.tracks-section h1{
  margin-bottom: 10px;
  font-size: var(--fs-24);
}
.tracks-section p{
  margin-bottom: 20px;
}
.tracks-container{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.track{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  text-align: left;
  width: 100%;
  max-width: 420px;
  height: 108px;
  box-shadow: 2px 0px 10px rgb(226, 226, 226);
  border-radius: 20px;
  padding: 0px 12px;
}
.track h1{
  max-width: 12ch;
}
.track-img{
  aspect-ratio: 1;
  height: 64px;
}
.track + .track {
}
.track-arrow{
  height: 40px;
  width: 40px;
}


/* -------PROGRAMS SECTION-------- */
.program{
  align-items: center;
  border-radius: 20px;
  max-width: 450px;
  padding: 25px 25px;
  text-align: center;
  box-shadow: 2px 0px 10px rgb(194, 194, 194);
}
.program img{
  aspect-ratio: 3/2;
  border-radius: 15px;
}
.programs-section{
  text-align: center;
}
.programs-section h1{
  font-size: var(--fs-24);
}
.programs-section p{

}
.programs-container{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.program > h1{
  font-size: var(--fs-24);
  text-transform: uppercase;
  letter-spacing: 3px;
}
.program + .program{
  margin-top: 20px;
}
.programs-line{
  background-image: url(../Assets/Images/Programs/line.png);
  width: 100%;
  height: 1px;
  margin: 10px auto;
}
.programs-info{
  padding: 15px 0px;
  width: 100%;
  height: 100%;
  justify-content: space-evenly;
  align-items: center;
}
.programs-links{
  text-align: start;
}
.program-link + .program-link {
  margin-top: 16px;
}
.programs-age{
  justify-content: center;
  align-items: center;
  background: var(--pes-navy);
  border-radius: 100%;
  height: 100px;
  aspect-ratio: 1;
}
.programs-age h3{
  font-size: var(--fs-11);
}
.programs-age h2{
  font-size: var(--fs-36);
  line-height: 115%;
}
.join-course{
  font-size: var(--fs-16);
  border: none;
  margin-top: 20px;
  display: grid;
  place-items: center;
  margin-inline: auto;
  background-color: var(--pes-orange);
  padding: 6px;
  width: 140px;
}


/* -----------INTEGRATIONS-------------- */
.integrations-section{
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.integrations-logos-container{
  margin: auto;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 16px;
  max-width: 320px;
}
.integrations-section h1{
  font-size: var(--fs-24);
  max-width: 23ch;
}
.integrations-section p{
  margin-top: 10px;
}
/* -------------FEATURES---------- */
.features-section{
  text-align: center;
}
.features-section h1{
  font-size: var(--fs-36);
}
.feature{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  text-align: center;
}
.feature + .feature{
}
.feature h2{
  margin-top: 50px;
  font-size: var(--fs-24);
}
.feature p{
  margin: 10px auto;
}
.feature img{
  width: 40%;
}
.assessments{
  margin: auto;
  text-align: left;
  max-width: 370px;
}
.assessments li{
  margin-left: 20px;
  width: fit-content;
}
/* -------TESTIMONIALS--------- */
.testimonials-section{
  text-align: center;
}
.testimonials-section *{
  margin-bottom: 10px;
}
.testimonials-section h1{
  font-size: var(--fs-24);
}
.testimonials-section button{
  border: none;
  margin-top: 10px;
  background-color: var(--pes-orange);
  width: auto;
  font-size: var(--fs-16);
  padding: 2px 20px;
}
.testimonials-img{
  margin: 0px auto;
}

/* --------FOOTER SECTION--------- */
.footer-section{
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: var(--pes-navy);
  padding: 20px;
  text-align: center;
  font-size: var(--fs-11);
  color: #B2B3CF;
}
.logo{

}
.newsletter-container{
  width: 100%;
}
.newsletter-container h1{
  margin: 20px 0px 20px 0px;
  width: 100%;
}
.newsletter-container input{
  text-align: center;
  border: 1px solid #83839A;
  background-color: var(--pes-navy);
  width: 100%;
  max-width: 400px;
}
.input-subscribe{
  display: flex;
  justify-content: center;
}
.newsletter-container button{
  border: none;
  background-color: #49BBBD;
  padding: 2px 20px;
  margin-left: 16px;
}
.footer-links{
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 20px auto;
}
.footer-links a{
  color: #B2B3CF;
  text-decoration: none;
}



/* -----------TABLET MEDIA QUERIES-------------- */
@media screen and (max-width: 800px) {
  .nav-tablet{
    display: none;
  }
  .track-description{
    display: none;
  }
  .section{
    max-width: 620px;
    margin-inline: auto;
  }
  .hero-section{
    max-width: none;
  }
  .badge{
    width: 100%;
    height: 100%;
    margin-inline: 10px;
  }
  .badges{
    width: 100%;
  }
}
  
@media screen and (min-width: 800px) {
  .nav-mobile{
    display: none;
  }
