  }
  .hero-section-buttons{
    font-size: var(--fs-16);
    margin: 0px;
  }
  .book-free-trial-button{
    height: auto;
    width: auto;
    padding: 16px;
  }
  .watch-button{
    height: 54px;
    width: 54px;
    margin-right: 10px;
  }
  .watch-button img{
    height: 22px;
  }
  .hero-section{
    aspect-ratio: 1024/624;
    height: fit-content;
  }
  

  /* ----------BADGES SECTION------------ */
  .badges-section h1{
    font-size: var(--fs-24);
    max-width: none;
  }
  .badges{
    margin-top: 20px;
    width: 100%;
  }
  .badge{
    height: 124px;
  }
  
  
  /* ----------WHO WE ARE SECTION------------- */
  .who-we-are-section h1{
    font-size: var(--fs-36);
  }
  .who-we-are-section p{
    font-size: 24px;
  }
  .frames-container{
    flex-direction: row;
    justify-content: space-between;
    height: fit-content;
  }
  .who-we-are-frame-container{
    width: calc(50% - 20px);
  }
  .who-we-are-frame-container + .who-we-are-frame-container{
    margin-top: 0px;
  }
  .frame{
    aspect-ratio: 41/28;
    margin: 0px;
  }
  .frame h1{
    font-size: var(--fs-24);
  }
  .join-button{
    padding: 12px 22px;
    width: auto;
    height: auto;
    font-size: var(--fs-16);
    margin-top: 20px;
  }


/* ----UNLEASH SECTION-------- */
  .unleash-section{
    display: flex;
    flex-direction: row;
    text-align: start;
    align-items: center;
    justify-content: space-between;
  }
  .unleash-section-text-container{
    width: calc(50% - 20px);
  }
  .unleash-section h1{
    margin: 0px;
    margin-bottom: 20px;
  }
  .unleash-video-container{
    width: calc(50% - 20px);
  }
  .unleash-video{
    width: 100%;
  }
  .unleash-video-caption{
    text-align: center;
  }
  /* -------SUCCESS SECTION---------- */
  .success-section h1{
    font-size: var(--fs-36);
    margin-bottom: 20px;
  }
  .success-section p{
    font-size: var(--fs-24);
    margin-bottom: 40px;
  }
  .success-numbers h1{
    margin: 0px;
    font-size: var(--fs-54);
    line-height: 100%;
  }
  .success-numbers{
    max-width: 820px;
    justify-content: space-between;
  }
  .success-numbers h2{
    font-size: var(--fs-24);
  }
  /* ----TRACKS SECTION----- */
  .tracks-section h1{
    font-size: var(--fs-36);
  }
  .tracks-section p{
    font-size: var(--fs-24);
  }
  .tracks-container{
    margin: 0px auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .track{
    flex-direction: column-reverse;
    height: auto;
    text-align: center;
    padding: 20px;
    height: 400px;
    width: fit-content;
  }
  .track h1{
    font-size: var(--fs-24);
    width: 100%;
    max-width: none;
    margin: 16px 0px;
  }
  .track-img{
    width: 100px;
    height: 100px;
    }
  .track-arrow{
    display: none;
  }
  .track{
    margin: 0px;
  }

  /* ----PROGRAMS SECTION---- */
  .programs-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .program + .program{
    margin: 0px;
  }
  .program{
    padding: 20px;
    margin: 40px;
    height: 652px;
    max-width: 420px;
  }
  .programs-section > h1{
    font-size: var(--fs-36);
  }
  .programs-section p{
    font-size: var(--fs-24);
  }
  .program a{
    font-size: var(--fs-24);
  }
  .program h1{
    margin-top: 10px;
    font-size: var(--fs-36);
  }
  .program h2{
    margin: 10px 0px;
  }
  .programs-line{
    height: 1px;
    margin: 0px;
  }
  .programs-info{
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .programs-age{
    height: 120px;
    width: 120px;
    margin-left: auto;
  }
  .programs-age h2{
    margin: 0px;
    font-size: var(--fs-54);
    line-height: 115%;
  }

  /* ------INTEGRATIONS SECTION-------- */
  .integrations-logos-container{
    width: fit-content;
    max-width: none;
    gap: 20px;
  }
  .integration-logo{
    width: 100%;
    height: 100%;
    max-width: 300px;
  }
  .integrations-section h1{
    font-size: var(--fs-36);
    margin: 20px auto;
  }
  .integrations-section p{
    font-size: var(--fs-24);
  }

  /* ----FEATURES SECTION----- */
  .features-section h3{
    font-size: var(--fs-24);
  }
  .features-section h3{
    margin-bottom: 20px;
  }
  .programs-section > h1{
    font-size: var(--fs-54);
  }
  .programs-section p{
    font-size: var(--fs-24);
  }
  .assessments{
    margin: auto;
    max-width: none;
  }
  .feature{
    flex-direction: row;
    justify-content: space-evenly;
    text-align: left;
    align-items: center;
  }
  .feature + .feature{
    margin-top: 50px;
  }
  .feature:nth-of-type(even){
    flex-direction: row-reverse;
  }
  .feature p{
    font-size: var(--fs-24);
  }

  /* ---TESTIMONIALS SECTION--- */
  .testimonials-section{
    text-align: left;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .testimonials-img{
    max-width: none;
    width: calc(40% - 10px);
  }
  .testimonials-section p{
    margin-bottom: 20px;
    font-size: var(--fs-24);
  }
  /* ---FOOTER SECTION--- */
  .logo{
    height: 150px;
  }
  .footer-section h1{
    font-size: var(--fs-24);
  }
  .input-subscribe{
    height: 60px;
    font-size: var(--fs-24);
  }
  .newsletter-container{
    width: 80%;
    margin-bottom: 80px;
  }
  .footer-links{
    font-size: var(--fs-24);
    margin-top: 0px;
    margin-bottom: 40px;
  }
  .footer-trademark{
    font-size: var(--fs-16);
    margin-bottom: 40px;
  }
}





/* ------DESKTOP STYLING------------ */
@media screen and (max-width: 1366px) {
  .nav-desktop{
    display: none;
  }
  .hero-section{
    display: flex;
  }
}
@media screen and (min-width: 1366px) {
  section + section{
    margin-top: 150px;
  }
  .section p{
    max-width: 70ch;
    margin-inline: auto;
  }
  .section{
    padding-inline: 150px;
    max-width: 1380px;
    margin-inline: auto;
  }
  .nav-tablet{
    display: none;
  }
  /* ------HERO SECTION---------- */
  .hero-section{
    max-width: none;
    background-image: url(../Assets/Images/hero-bg-flat.png);
    background-position: center;
    display: flex;
    flex-direction: column;
    max-height: 100vh;
  }
  .hero-text-section{
    padding-top: 150px;
    padding-left: 0px;
  }
  .hero-text-section p{
    max-width: 35ch;
  }
  .nav-desktop{
    padding: 20px 0px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }
  .pes-logo{
    margin-right: auto;
    height: 114px;
    width: 240px;
  }
  .nav-links{
    margin-right: 50px;
  }
  /* BADGES SECTION */
  .badges-section{
    margin-top: 100px;
    max-width: 1100px;
  }
  /* ------WHO WE ARE SECTION------- */
  .who-we-are-section > h1, .success-section h1, .tracks-section > h1, .features-section h1{
    font-size: var(--fs-54);
  }
  /* UNLEASH SECTION */
  .unleash-section h1{
    font-size: var(--fs-36);
  }
  .unleash-section p{
    font-size: var(--fs-24);
  }
  /* SUCCESS SECTION */
  .success-numbers h1{
    max-width: none;
    font-size: var(--fs-81);
  }
  /* TRACKS SECTION */
  .tracks-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .track{
    margin: 25px;
    width: 450px;
    height: 443px;
  }
  /* FEATURES SECTION */
  .features-section h2{
    font-size: var(--fs-36);
  }
  .feature{
    justify-content: center;
  }
  .feature + .feature{
    margin-top: 100px;
  }
  .feature img{
    height: 100%;
    width: auto;
  }
  .feature-text{
    width: 50%;
  }
  /* TESTIMONIALS SECTION */
  .testimonials-section{
    width: auto;
    margin-inline: auto;
  }
  .testimonials-section h1{
    font-size: var(--fs-36);
  }
  .testimonials-section-text{
    width: 50%;
  }
  .testimonials-img{
    margin: 0px;
    width: fit-content;
  }



}